<template>
    <div class="mix card position-relative" :class="filters" v-if="comment">
        <div class="card-body">
            <div class="name" v-html="comment.nickname"></div>
            <div class="rating" v-html="rating"></div>
            <div class="review" v-text="comment.review"></div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Comment",
    props: ["comment"],
    computed: {
        rating() {
            let output = "";
            let maxStars = 5;
            let rating = this.comment.rating;
            while (maxStars) {
                output +=
                    '<span class="' +
                    (rating > 0 && "filled-star") +
                    '">☆</span>';
                rating--;
                maxStars--;
            }

            return output;
        },
        filters() {
            return [this.comment.platform, this.comment.recipient];
        },
    },
};
</script>

<style>
.mix.iOS {
    border-top: 4px solid #0062cc;
}
.mix.Android {
    border-top: 4px solid #dc3545;
}
.rating {
    unicode-bidi: bidi-override;
}
.rating > span {
    display: inline-block;
    position: relative;
    width: 1.1em;
}
.filled-star:before {
    content: "\2605";
    position: absolute;
    color: rgba(244, 208, 46, 1);
}
.review {
    font-size: 0.8rem;
}

.mix {
    display: inline-block;
    vertical-align: top;
}
.mix {
    background: #fff;
    border-top: 0.5rem solid currentColor;
    border-radius: 2px;
    margin-bottom: 1rem;
    position: relative;
}

