<template>
    <div id="app">
        <div
            class="bg-primary text-white pt-2 pb-3 d-flex flex-wrap justify-content-center text-center"
        >
            <div class="d-flex flex-column m-4" role="toolbar">
                <div class="controls-heading">Платформа</div>
                <div class="controls" role="group">
                    <button
                        type="button"
                        class="btn btn-light border border-secondary btn-filter"
                        :class="{'active': platformFilter === 'all' || !platformFilter}"
                        @click="togglePlatformFilter('all')"
                    >Все</button>
                    <button
                        type="button"
                        class="btn btn-light border border-secondary btn-filter"
                        :class="{'active': filters.includes('.Android')}"
                        @click="togglePlatformFilter('.Android')"
                    >
                        <i class="fab fa-android"></i>Android
                    </button>
                    <button
                        type="button"
                        class="btn btn-light border border-secondary btn-filter"
                        :class="{'active': filters.includes('.iOS')}"
                        @click="togglePlatformFilter('.iOS')"
                    >
                        <i class="fab fa-apple"></i>iOS
                    </button>
                </div>
            </div>
            <div class="d-flex flex-column m-4" role="toolbar">
                <div class="controls-heading">Тема</div>
                <div class="controls" role="group">
                    <button
                        type="button"
                        class="btn btn-light border border-secondary btn-filter"
                        :class="{'active': recipientFilter === 'all' || !recipientFilter}"
                        @click="toggleRecipientFilter('all')"
                    >Все</button>
                    <button
                        type="button"
                        class="btn btn-light border border-secondary btn-filter"
                        :class="{'active': filters.includes('.app')}"
                        @click="toggleRecipientFilter('.app')"
                    >
                        <i class="fas fa-mobile-alt"></i>Приложение
                    </button>
                    <button
                        type="button"
                        class="btn btn-light border border-secondary btn-filter"
                        :class="{'active': filters.includes('.site')}"
                        @click="toggleRecipientFilter('.site')"
                    >
                        <i class="fas fa-globe"></i>Сайт
                    </button>
                    <button
                        type="button"
                        class="btn btn-light border border-secondary btn-filter"
                        :class="{'active': filters.includes('.backend')}"
                        @click="toggleRecipientFilter('.backend')"
                    >
                        <i class="fas fa-server"></i>Бэкэнд
                    </button>
                    <button
                        type="button"
                        class="btn btn-light border border-secondary btn-filter"
                        :class="{'active': filters.includes('.unknown')}"
                        @click="toggleRecipientFilter('.unknown')"
                    >
                        <i class="fas fa-question"></i>Неизвестно
                    </button>
                </div>
                <div class="recipient-description" v-html="recipientDescription"></div>
            </div>

            <div class="d-flex flex-column m-4" role="toolbar">
                <div class="controls-heading">Комментариев</div>
                <div class="stats">
                    {{ commentsPercentage }}% |
                    <i class="far fa-comment"></i>
                    {{commentsCount}}
                </div>
            </div>

            <div class="d-flex flex-column m-4" role="toolbar">
                <div class="controls-heading">Рейтинг</div>
                <div class="stats">
                    <i class="far fa-star"></i>
                    {{rating}}
                </div>
            </div>
        </div>
        <div id="mixer" class="container-fluid mt-4">
            <comment v-for="(comment, index) in comments" :key="index" :comment="comment"></comment>
        </div>
    </div>
</template>

<script>
import Comment from "./components/Comment.vue";
import androidComments from "./comments/android";
import iosComments from "./comments/ios";
import mixitup from "mixitup";

export default {
    name: "App",
    data() {
        return {
            platformFilter: "",
            recipientFilter: "",
            rating: 0,
            commentsCount: 0,
            commentsPercentage: 0,
            mixer: null,
            recipientDescription: "&nbsp;",
            recipientDescriptions: {
                app: "UI/UX, фичи, недоработки",
                site: "скорость webview, покупки, поиск, фильтры",
                backend:
                    "регистрация/авторизация, sms, история покупок, профиль",
                unknown: "&nbsp;",
            },
        };
    },
    computed: {
        comments() {
            const comments = [];

            androidComments.forEach((comment) => {
                comment.platform = "Android";
                comments.push(comment);
            });
            iosComments.forEach((comment) => {
                comment.platform = "iOS";
                comments.push(comment);
            });

            return comments;
        },
        filters() {
            return this.platformFilter + this.recipientFilter || "all";
        },
    },
    mounted() {
        this.mixer = mixitup(document.getElementById("mixer"));
        this.updateResults(this.filters);
    },
    watch: {
        filters(filter) {
            this.updateResults(filter);
        },
    },
    methods: {
        togglePlatformFilter(filter) {
            this.platformFilter = filter === "all" ? "" : filter;
            this.mixer.filter(this.filters);
        },
        toggleRecipientFilter(filter) {
            this.recipientFilter = filter === "all" ? "" : filter;
            this.mixer.filter(this.filters);
        },
        updateResults(filter) {
            let comments = [];

            if (filter === "all") {
                comments = this.comments;
            } else if (this.platformFilter && !this.recipientFilter) {
                this.comments.forEach((comment) => {
                    if (this.platformFilter === "." + comment.platform) {
                        comments.push(comment);
                    }
                });
            } else if (this.recipientFilter && !this.platformFilter) {
                this.comments.forEach((comment) => {
                    if (this.recipientFilter === "." + comment.recipient) {
                        comments.push(comment);
                    }
                });
            } else {
                this.comments.forEach((comment) => {
                    if (
                        this.platformFilter === "." + comment.platform &&
                        this.recipientFilter === "." + comment.recipient
                    ) {
                        comments.push(comment);
                    }
                });
            }

            let commentsCount = comments.length;
            let totalRating = 0;
            comments.forEach((comment) => {
                totalRating += comment.rating;
            });

            this.rating = Number((totalRating / commentsCount).toFixed(2));
            this.commentsCount = commentsCount;
            this.commentsPercentage = Math.ceil(
                (Math.ceil(commentsCount) / this.comments.length) * 100
            );
            console.log(this.recipientFilter);
            this.recipientDescription = this.recipientFilter
                ? this.recipientDescriptions[this.recipientFilter.substring(1)]
                : "&nbsp";
        },
    },
    components: {
        Comment,
    },
};
</script>

<style>
#app .btn-filter.active {
    color: #fff;
    background-color: #28a745;
    border-color: #28a745;
}
.btn-filter {
    min-width: 110px;
    margin: 0.5rem 0.25rem;
}
.btn-filter > i {
    margin-right: 7px;
    display: inline-block;
}
.stats {
    margin-top: 1rem;
}
.recipient-description {
    font-size: 0.9rem;
    margin-bottom: -1.5rem;
}
@media (max-width: 800px) {
    .btn-filter {
        min-width: 70px;
    }
}
</style>
